/* Zine Component Styles - Mobile First Design */

.zine-container {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

/* Loading State */
.zine-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  padding: 2rem;
}

.loading-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 2rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.zine-loading h2 {
  font-size: 2rem;
  margin: 0 0 1rem 0;
  font-weight: 700;
}

.zine-loading p {
  font-size: 1.1rem;
  opacity: 0.8;
  margin: 0;
}

/* Zine Header */
.zine-header {
  width: 100%;
  padding: 2rem 1rem;
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.zine-header h1 {
  font-size: 2.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.zine-header p {
  font-size: 1.1rem;
  margin: 0 0 2rem 0;
  opacity: 0.9;
}

/* Navigation */
.zine-navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.nav-btn {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  min-width: 100px;
}

.nav-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.6);
  transform: translateY(-2px);
}

.nav-btn:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  opacity: 0.5;
}

.page-counter {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  font-weight: 600;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Zine Content */
.zine-content {
  width: 100%;
  max-width: 500px;
  padding: 2rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
}

.zine-page {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.zine-image {
  width: 100%;
  max-width: 400px;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
  cursor: pointer;
}

.zine-image:hover {
  transform: scale(1.02);
}

.page-info {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  max-width: 400px;
}

.page-info h3 {
  font-size: 1.3rem;
  margin: 0 0 0.8rem 0;
  font-weight: 600;
}

.page-info p {
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
  line-height: 1.5;
}

/* Thumbnails */
.zine-thumbnails {
  width: 100%;
  padding: 2rem 1rem;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.thumbnails-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
  gap: 0.8rem;
  max-width: 500px;
  margin: 0 auto;
}

.thumbnail {
  position: relative;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
  aspect-ratio: 1;
}

.thumbnail:hover {
  transform: translateY(-3px);
}

.thumbnail.active {
  border: 3px solid #fff;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

.thumbnail-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.thumbnail-number {
  position: absolute;
  top: 4px;
  right: 4px;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  font-size: 0.7rem;
  font-weight: 600;
  padding: 0.2rem 0.4rem;
  border-radius: 8px;
  min-width: 20px;
  text-align: center;
}

/* Footer */
.zine-footer {
  width: 100%;
  padding: 2rem 1rem;
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.zine-footer p {
  margin: 0.5rem 0;
  font-size: 0.9rem;
  opacity: 0.8;
}

/* Touch Gesture Hints */
.zine-content::before {
  content: '👆 Swipe left/right to navigate';
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.zine-content:hover::before {
  opacity: 1;
}

/* Responsive Design */
@media (min-width: 768px) {
  .zine-header h1 {
    font-size: 3rem;
  }
  
  .zine-content {
    max-width: 700px;
    padding: 3rem 2rem;
  }
  
  .zine-image {
    max-width: 600px;
  }
  
  .page-info {
    max-width: 600px;
    padding: 2rem;
  }
  
  .thumbnails-container {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
    gap: 1rem;
    max-width: 700px;
  }
  
  .zine-content::before {
    display: none;
  }
}

@media (min-width: 1024px) {
  .zine-container {
    padding: 0 2rem;
  }
  
  .zine-header {
    padding: 3rem 2rem;
  }
  
  .zine-content {
    max-width: 900px;
    padding: 4rem 2rem;
  }
  
  .zine-image {
    max-width: 800px;
  }
  
  .page-info {
    max-width: 800px;
    padding: 2.5rem;
  }
  
  .thumbnails-container {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 1.2rem;
    max-width: 900px;
  }
}

/* Smooth Scrolling */
.zine-container {
  scroll-behavior: smooth;
}

/* Animation for page transitions */
.zine-page {
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Touch feedback */
.zine-content:active {
  transform: scale(0.98);
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .zine-image,
  .thumbnail,
  .nav-btn {
    transition: none;
  }
  
  .loading-spinner {
    animation: none;
  }
  
  .zine-page {
    animation: none;
  }
} 