.photo-gallery-section {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.gallery-header {
  padding: 3rem 2rem;
  text-align: center;
  width: 100%;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.gallery-header h2 {
  font-size: 3rem;
  margin: 0 0 1rem 0;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.gallery-header p {
  font-size: 1.2rem;
  margin: 0 0 2rem 0;
  opacity: 0.9;
}

.google-photos-embed-container {
  width: 100%;
  max-width: 1200px;
  margin: 2rem auto;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 2rem;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.embed-header {
  text-align: center;
  margin-bottom: 2rem;
}

.embed-header h3 {
  font-size: 1.8rem;
  margin: 0 0 0.5rem 0;
  color: white;
  font-weight: 700;
}

.embed-header p {
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
}

.photos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.album-photo-item {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  background: white;
  box-shadow: 0 8px 25px rgba(0,0,0,0.15);
  transition: transform 0.3s ease;
}

.album-photo-item:hover {
  transform: translateY(-5px);
}

.album-photo {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.photo-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));
  color: white;
  padding: 1rem;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

.album-photo-item:hover .photo-overlay {
  transform: translateY(0);
}

.photo-title {
  display: block;
  font-weight: 600;
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
}

.photo-description {
  display: block;
  font-size: 0.8rem;
  opacity: 0.9;
}

.photo-placeholder {
  grid-column: 1 / -1;
  text-align: center;
  padding: 3rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  border: 2px dashed rgba(255, 255, 255, 0.3);
}

.photo-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  color: white;
}

.photo-error {
  grid-column: 1 / -1;
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  color: white;
}

.no-photos {
  grid-column: 1 / -1;
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  color: white;
}

.retry-button {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  cursor: pointer;
  margin-top: 1rem;
  transition: all 0.3s ease;
}

.retry-button:hover {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.6);
}

.album-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  display: block;
}

.album-preview h3 {
  font-size: 2rem;
  margin: 0 0 1rem 0;
  color: white;
  font-weight: 700;
}

.album-preview p {
  font-size: 1.1rem;
  margin: 0 0 2rem 0;
  opacity: 0.9;
  line-height: 1.6;
}

.album-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.view-album-button,
.open-photos-button {
  display: inline-block;
  padding: 1rem 2rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.view-album-button {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border-color: rgba(255, 255, 255, 0.3);
}

.view-album-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(102, 126, 234, 0.4);
  border-color: rgba(255, 255, 255, 0.6);
}

.open-photos-button {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border-color: rgba(255, 255, 255, 0.3);
}

.open-photos-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-3px);
  border-color: rgba(255, 255, 255, 0.6);
}

.album-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.feature-item:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
}

.feature-icon {
  font-size: 1.5rem;
}

.feature-text {
  font-size: 0.9rem;
  color: white;
  font-weight: 500;
}

.gallery-nav {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  z-index: 10;
}

.gallery-nav:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
  border-color: rgba(255, 255, 255, 0.6);
}

.gallery-nav:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
}

.gallery-container {
  width: 100%;
  max-width: 1000px;
  height: 600px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0,0,0,0.3);
  background: white;
  position: relative;
}

.gallery-image {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: #666;
  font-size: 1rem;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.image-display {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.photo-display {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

.gallery-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

.gallery-photo:hover {
  transform: scale(1.05);
}

.photo-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.photo-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  transition: all 0.3s ease;
  opacity: 0;
}

.photo-display:hover .photo-overlay {
  opacity: 1;
}

.photo-display:hover .photo-thumbnail {
  transform: scale(1.05);
}

.photo-overlay .photo-title {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  text-align: center;
}

.album-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.4);
  background: linear-gradient(135deg, #2980b9, #1f5f8b);
}

.album-button .album-subtitle {
  font-size: 0.9rem;
  opacity: 0.8;
  margin-top: 0.5rem;
  text-align: center;
  line-height: 1.4;
}

.album-button .album-cta {
  font-size: 0.8rem;
  opacity: 0.6;
  margin-top: 0.5rem;
  text-align: center;
  font-style: italic;
}

.uploaded-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 8px;
}

.placeholder-image {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #e74c3c, #f39c12);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  position: relative;
}

.image-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.image-number {
  font-size: 1.4rem;
  font-weight: 700;
}

.image-description {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.image-description-text {
  font-size: 0.9rem;
  opacity: 0.9;
  line-height: 1.4;
}

.image-counter {
  font-size: 0.8rem;
  opacity: 0.7;
  font-weight: 500;
}

.gallery-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin: 2rem 0;
}

.photo-counter {
  font-size: 1.1rem;
  font-weight: 600;
  margin: 1rem 0;
  opacity: 0.9;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.5rem 1.5rem;
  border-radius: 25px;
  backdrop-filter: blur(10px);
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: #ddd;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background: #3498db;
  transform: scale(1.2);
}

.indicator:hover {
  background: #3498db;
  opacity: 0.7;
}

.upload-section {
  margin-top: 2rem;
  text-align: center;
}

.uploaded-photos-section {
  margin-top: 3rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

.uploaded-photos-section h3 {
  margin: 0 0 1.5rem 0;
  font-size: 1.5rem;
  color: white;
}

.uploaded-photos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-top: 1rem;
}

.uploaded-photo-item {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0,0,0,0.15);
  transition: transform 0.3s ease;
}

.uploaded-photo-item:hover {
  transform: translateY(-5px);
}

.uploaded-photo {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.photo-info {
  padding: 1rem;
  text-align: left;
}

.photo-name {
  display: block;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.photo-description {
  display: block;
  color: #666;
  font-size: 0.8rem;
}

.upload-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.refresh-button {
  background: linear-gradient(135deg, #f39c12, #e67e22);
  color: white;
  padding: 1rem 2rem;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  font-size: 1rem;
  box-shadow: 0 4px 15px rgba(243, 156, 18, 0.3);
}

.refresh-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(243, 156, 18, 0.4);
  background: linear-gradient(135deg, #e67e22, #d35400);
}

.upload-button {
  background: linear-gradient(135deg, #27ae60, #2ecc71);
  color: white;
  padding: 1rem 2rem;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
  box-shadow: 0 4px 15px rgba(39, 174, 96, 0.3);
  border: none;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
}

.upload-button:hover:not(.uploading) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(39, 174, 96, 0.4);
}

.upload-button.uploading {
  background: linear-gradient(135deg, #95a5a6, #7f8c8d);
  cursor: not-allowed;
  transform: none;
}

.upload-button.uploading::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

.upload-hint {
  margin-top: 0.75rem;
  color: #666;
  font-size: 0.9rem;
  font-style: italic;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.4;
}

/* Responsive Design */
@media (max-width: 768px) {
  .carousel-container {
    width: 300px;
    height: 225px;
  }
  
  .photo-overlay {
    padding: 1.5rem;
  }
  
  .photo-overlay .photo-title {
    font-size: 1rem;
  }
  
  .photo-overlay .photo-description {
    font-size: 0.8rem;
  }
  
  .image-description {
    padding: 0.75rem;
  }
  
  .image-description-text {
    font-size: 0.8rem;
  }
  
  .image-counter {
    font-size: 0.7rem;
  }
  
  .upload-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .carousel-container {
    width: 250px;
    height: 188px;
  }
  
  .photo-overlay {
    padding: 1rem;
  }
  
  .photo-overlay .photo-title {
    font-size: 0.9rem;
  }
  
  .photo-overlay .photo-description {
    font-size: 0.7rem;
  }
  
  .carousel-nav {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
  
  .upload-button, .refresh-button {
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
  }
}

/* Album Info Styles */
.album-info {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.album-count {
  font-size: 0.9rem;
  color: #666;
  background: #f8f9fa;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid #e9ecef;
}

.album-link-text a {
  color: #3498db;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.album-link-text a:hover {
  color: #2980b9;
  text-decoration: underline;
}

.google-photos-link {
  color: #4285f4;
  text-decoration: none;
  font-weight: 500;
  margin-left: 1rem;
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  background: rgba(66, 133, 244, 0.1);
  transition: all 0.3s ease;
}

.google-photos-link:hover {
  background: rgba(66, 133, 244, 0.2);
  color: #1a73e8;
  text-decoration: none;
}

@media (max-width: 768px) {
  .album-info {
    flex-direction: column;
    gap: 1rem;
  }
}

/* Photo Overlay Styles */
.photo-overlay .photo-description {
  font-size: 0.9rem;
  opacity: 0.9;
  margin-bottom: 1rem;
  text-align: center;
  line-height: 1.4;
  max-width: 250px;
}

.photo-overlay .photo-view-full {
  color: #3498db;
  background: rgba(255, 255, 255, 0.9);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.8rem;
  transition: all 0.3s ease;
}

.photo-overlay .photo-view-full:hover {
  background: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

/* No Photos and Error States */
.no-photos-state,
.error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  color: #666;
}

.no-photos-state p,
.error-state p {
  margin: 0.5rem 0;
  font-size: 1rem;
}

.no-photos-state .google-photos-link {
  margin-top: 1rem;
}

.retry-button {
  background: #3498db;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.retry-button:hover {
  background: #2980b9;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
} 